import html2canvas from 'html2canvas';
import jsPDF from 'jspdf';

const a4WidthMm = 210;
const a4HeightMm = 297;

const a4WidthInches = 8.3;
const a4HeightInches = 11.7;

const a4WidthPixels = () => {
    return mmToPx(a4WidthMm);
};

const a4HeightPixels = () => {
    return mmToPx(a4HeightMm);
};


const pxToMm = (px) => {
    return Math.floor(px / document.getElementById('myMm').offsetHeight);
};

const mmToPx = (mm) => {
    return document.getElementById('myMm').offsetHeight * mm;
};

export let htmlToPdf = () => {
    const input = document.getElementById("html2pdf");
    const inputHeightMm = pxToMm(input.scrollHeight);
    const numPages = inputHeightMm <= a4HeightMm ? 1 : Math.floor(inputHeightMm / a4HeightMm) + 1;

    html2canvas(input, { windowHeight: input.scrollHeight }).then((canvas) => {

        let pdf = null;

        if (inputHeightMm > a4HeightMm) {

            pdf = new jsPDF({
                orientation: 'portrait',
                unit: 'mm',
                format: [a4HeightPixels() * numPages, a4WidthPixels()]
            });

        } else {
            // standard a4
            pdf = new jsPDF();

        }

        if (pdf) {
            const imgData = canvas.toDataURL('image/png');
            pdf.addImage(imgData, 'PNG', 0, 0);
            pdf.save(`Stickers to print.pdf`);
        }

    });
}